import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'

Vue.use(Vuex);

// init
const state = {
  count: 10,
  isLoading: false
};

//mutations
const mutations = {
  INCREMENT(state) {
    state.count++
  },
  DECREMENT(state) {
    state.count--
  },
  updateLoadingStatus (state, payload) {
    state.isLoading = payload.isLoading
  }
};

//实例化
export default new Vuex.Store({
  actions,
  getters,
  state,
  mutations
})



