import Vue from 'vue'
import Router from 'vue-router'
import store from '../vuex/store'
Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [
    {

      path: '/',
      name: 'index',
      component: () => import('@/pages/main'),
      redirect: '/index',
      children: [
        {
          path: 'index',
          // name: 'index',
          meta: {
            title: '首页'
          },
          component: () => import('@/pages/index')
        },
        {
          path: '/curriculum',
          name: 'curriculum',
          meta: {
            title: '课程'
          },
          component: () => import('@/pages/curriculum/curriculum')
        },
        {

          path: '/discover',
          name: 'discover',
          meta: {
            title: '发现'
          },
          component: () => import('@/pages/discover/discover')
        },
        {

          path: '/task',
          name: 'task',
          meta: {
            title: '任务'
          },
          component: () => import('@/pages/task/task')
        },
        {

          path: '/mine',
          name: 'mine',
          meta: {
            title: '个人中心'
          },
          component: () => import('@/pages/mine/mine')
        }


      ]
    },
    // 官方课程
    {
      path: '/officialCourses',
      name: 'officialCourses',
      component: () => import('@/pages/curriculum/officialCourses/officialCourses'),
    },
    // 非官方课程
    {
      path: '/unofficialCourses',
      name: 'unofficialCourses',
      component: () => import('@/pages/curriculum/unofficialCourses/unofficialCourses'),
    },
    // 课堂检测
    {
      path: '/classroomTesting',
      name: 'classroomTesting',
      meta: {
        title: '课堂检测'
      },
      component: () => import('@/pages/curriculum/classroomTesting/classroomTesting'),
    },

    //  音频
    {
      path: '/curriculumAudio',
      name: 'curriculumAudio',
      component: () => import('@/pages/curriculumDetails/curriculumAudio'),
    },
    // 视频
    {
      path: '/curriculumVideo',
      name: 'curriculumVideo',
      component: () => import('@/pages/curriculumDetails/curriculumVideo'),
    },
    // 学员见证 详情
    {

      path: '/discover/studentDetails',
      name: 'studentDetails',
      meta: {
        title: '详情'
      },
      component: () => import('@/pages/discover/discoverDetails/studentDetails')
    },
    // 作业赏析 详情
    {

      path: '/discover/jobDetails',
      name: 'jobDetails',
      meta: {
        title: '详情'
      },
      component: () => import('@/pages/discover/discoverDetails/jobDetails')
    },
    // 作业上传
    {

      path: '/curriculum/task',
      meta: {
        title: '详情'
      },
      component: () => import('@/pages/curriculum/task/task')
    },
    // 直播
    {
      path: '/curriculumLiveBroadcast',
      name: 'curriculumLiveBroadcast',
      component: () => import('@/pages/curriculumDetails/curriculumLiveBroadcast'),
    },
    // 购买课程
    {
      path: '/buyCurriculum',
      name: 'buyCurriculum',
      component: () => import('@/pages/curriculum/buyCurriculum/buyCurriculum'),
    },
    // 支付详情
    {
      path: '/buyInfo',
      name: 'buyInfo',
      component: () => import('@/pages/curriculum/buyCurriculum/buyInfo'),
    },
    {
      path: '/signIn',
      name: 'signIn',
      meta: {
        title: '每日签到'
      },
      component: () => import('@/pages/task/signIn'),
    },
    {
      path: '/userInfo',
      name: 'userInfo',
      meta: {
        title: '个人信息'
      },
      component: () => import('@/pages/mine/userInfo'),
    },
    {
      path: '/userName/:id',
      name: 'userName',
      meta: {
        title: '修改昵称'
      },
      component: () => import('@/pages/mine/userName'),
    },
    {
      path: '/bindTel/:type',
      name: 'bindTel',
      component: () => import('@/pages/mine/bindTel'),
    },
    {
      path: '/updateTel',
      name: 'updateTel',
      meta: {
        title: '修改手机号'
      },
      component: () => import('@/pages/mine/updateTel'),
    },
    {
      path: '/purchaseCourse',
      name: 'purchaseCourse',
      meta: {
        title: '购买的课程'
      },
      component: () => import('@/pages/mine/purchaseCourse'),
    },
    {
      path: '/myLive',
      name: 'myLive',
      meta: {
        title: '我的直播'
      },
      component: () => import('@/pages/mine/myLive'),
    },
    {
      path: '/account',
      name: 'account',
      meta: {
        title: '账户余额'
      },
      component: () => import('@/pages/mine/account'),
    },
    {
      path: '/cashOut',
      name: 'cashOut',
      meta: {
        title: '账户余额'
      },
      component: () => import('@/pages/mine/cashOut'),
    },
    {
      path: '/myTask',
      name: 'myTask',
      meta: {
        title: '我的作业'
      },
      component: () => import('@/pages/mine/myTask'),
    },
    {
      path: '/myCollect',
      name: 'myCollect',
      meta: {
        title: '我的收藏'
      },
      component: () => import('@/pages/mine/myCollect'),
    },
    {
      path: '/aboutUs',
      name: 'aboutUs',
      meta: {
        title: '关于我们'
      },
      component: () => import('@/pages/mine/aboutUs'),
    },
    {
      path: '/problem',
      name: 'problem',
      meta: {
        title: '常见问题'
      },
      component: () => import('@/pages/mine/problems'),
    }



  ]

})


router.beforeEach((to, from, next) => {
  store.commit('updateLoadingStatus', {isLoading: true});
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})

router.afterEach(function (to) {
  store.commit('updateLoadingStatus', {isLoading: false});
});


export default router



